.slick-active .Right {
  animation-duration: 2s;
  animation-fill-mode: both;
  animation-name: Right;
}

@keyframes Right {
  0% {
    opacity: 0;
    transform: translateX(700px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

.slick-active .rotateIn {
  transform: rotate(0deg) scale(1);
  opacity: 1;
  transition: all 2s;
}

.reset-animation .slide-item__text {
  transform: rotate(-360deg) scale(0);
  transition: all 0s;
}

.slick-active .zoomIn {
  opacity: 1;
  transform: scale(1);
}
